@use 'foundation' as *;

.sm\:u-w-fit {
    width: fit-content;
  }
  .sm\:u-w-full {
    width: 100%;
  }
  .sm\:u-w-1\/3 {
    width: calc(100% / 3);
  }
  .sm\:u-w-1\/2 {
    width: 50%;
  }
  .sm\:u-w-2\/3 {
    width: calc(200% / 3);
  }
  .sm\u-w-1-5\/2 {
    width: 75%;
  }
  .sm\:u-w-screen {
    width: 100vw;
  }

  @include mq(md) {
    .u-w-fit {
        width: fit-content;
      }
      .u-w-full {
        width: 100%;
      }
      .u-w-1\/3 {
        width: calc(100% / 3);
      }
      .u-w-1\/2 {
        width: 50%;
      }
      .u-w-2\/3 {
        width: calc(200% / 3);
      }
      .u-w-1-5\/2 {
        width: 75%;
      }
      .u-w-screen {
        width: 100vw;
      }
  }